.reviews {

    & .card {
        display: flex;
        flex-direction: column;
        height: 320px;

        & .review-info {
            display: flex;
            gap: 10px;
            font-size: 14px;
        }

        & .review-content {
            display: -webkit-box;
            -webkit-line-clamp: 7;
            -webkit-box-orient: vertical;
            overflow: hidden;
            text-overflow: ellipsis;
        }

        & .review-content-link {
            margin-top: auto;
        }
    }
}

