.breadcrumb {
	display: flex;
    align-items: center;
	padding: 8px 0;

	& span {
		font-size: 12px;

		& a {
			font-size: 12px;
			font-weight: 500;
			color: #212529;
			text-decoration: none;

			&:hover {
				color: #16a34a;
			}
		}

		&.breadcrumbs__separator {
			display: flex;

			& svg path {
				fill: var(--separator-coror);
			}
		}

	}

	& .breadcrumb-item {
        text-transform: none;
        font-size: 14px;
        font-weight: 700;
        padding-left: 0;
        display: inline-block;

        &::after {
            display: none;
        }

        &.active {
            font-weight: 600;
        }
    }
}

.popular-tour {
    height: 300px;

    & img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    & .content {
        bottom: 0;
        left: 0;
        padding: 10px;
    }

    & .link-location {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
}
