.popular-glampings-home {
    gap: 30px;

    @media (max-width: 1199px) {
        gap: 20px;
    }
}

.glampings-items,
.locations-items {
    display: flex;
    gap: 30px;
    flex-wrap: wrap;
}

// .locations-items {
