//     gap: 30px;
//     flex-wrap: wrap;
// }

.card-items {
    position: relative;
    display: flex;
    flex-direction: column;
    width: calc((100% - 90px) / 4);
    border: 1px solid var(--border-widget-color);
    border-radius: 8px;
    overflow: hidden;
    box-shadow: 0 0 3px rgba(60, 72, 88, 0.15);

    &:hover {
        box-shadow: 0 0 13px rgba(60, 72, 88, .15);
    }

    @media (max-width: 1199px) {
        width: calc((100% - 40px) / 3);
    }

    @media (max-width: 767px) {
        width: calc((100% - 20px) / 2);
    }

    @media (max-width: 567px) {
        width: 100%;
    }

    &__link {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        z-index: 2;

        &:hover ~ .card-items__content a {
            color: #16a34a;
        }

        &:hover ~ .card-items__img span img {
            transform: scale(1.1);
        }
    }

    &__img {
        position: relative;
        flex: none;
        width: auto;
        overflow: hidden;
        aspect-ratio: 1 / 0.6;

        & span {
            display: block;
            width: 100%;
            height: 100%;

            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                transition: 0.3s all;

                @media (max-width: 567px) {
                    height: auto;
                }

                &:hover {
                    background-size: 180%;
                }
            }
        }

        &__info {}
    }

    &__content {
        display: flex;
        flex-direction: column;
        padding: 15px;
        height: 100%;

        & a {
            line-height: 25px;
            font-size: 16px;
            font-weight: 700;
            color: var(--text-color);
            font-weight: 600;
            text-decoration: none;
            margin-bottom: 13px;

            &:hover {
                color: #16a34a;
            }
        }

        &__adress {
            margin-bottom: 14px;
            font-size: 13px;
            line-height: 15px;

            & svg {
                margin-bottom: 2px;

                & path {
                    fill: var(--bs-gray);
                }

            }
        }

        &__reviews {
            margin-top: auto;

            &-stars {
                display: inline-flex;
                padding: 0 8px;
                border-radius: 4px;
                color: #FFFFFF;
                background: #16a34a;
            }

            &-count {
                font-size: 12px;
            }
        }

        &__bottom {
            display: flex;
            justify-content: space-between;

            &-min_placement {
                display: flex;
                align-items: center;
                font-weight: 500;

                & svg {
                    margin-right: 6px;
                }
            }

            &-price {
                display: flex;
                align-items: center;
                font-size: 13px;
                font-weight: 500;

                & svg {
                    margin-right: 6px;
                }

                & .price-color {
                    font-size: 20px;
                    font-weight: 700;
                    color: #16a34a;
                }
            }
        }

        // &__bottom-price {}
    }
}

.loc-item {
    height: 200px;

    &__link {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        z-index: 2;

        // &:hover ~ .card-glampings__content a {
        //     color: #16a34a;
        // }

        &:hover ~ .loc-item__img {
            transform: scale(1.1);
        }
    }

    &__img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        filter: brightness(0.7);
        transition: 0.3s all;
    }

    &__text {
        position: absolute;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        color: #FFFFFF;
        z-index: 1;

        &__title {
            font-weight: 700;
            text-align: center;
        }

        &__count {}
    }
}
